interface IPromiseData<T> {
    promise: Promise<T>;
    resolver: (value: T | PromiseLike<T>) => void;
    reject: (reason?: any) => void;
    startTimer: (timeout: number) => void;
}

interface IRequestInfo{
    type: string;
    guid: string;
}

interface IResource {
    resourceId: string;
    resource: string | Buffer;
    resourceType: string;
}

interface serviceWorkerRequestResponse {
    resourceModel: string;
    resourceId: string;
    resourceType: string;
};

interface IImportFile {
    type: "entity" | "qjson" | "process" | "bpmn" | "flow" | "png" | "jpg" | "jpeg" | "svg" | "gif" | "json"; //Bunlar ilerde eklenecekler.
}


const binaryFileTypes: Array<string> = ["gif", "png", "jpeg", "jpg", "svg", "woff", "woff2", "ttf"];

const swTimeout = 15000;

//some platforms have syncronous resolve problem (ex: IOS JavascriptCore)
declare var doNothing: (() => void) | undefined;
let syncResolved = false as boolean;
new Promise<void>(res => res()).then(() => syncResolved = true);
typeof (doNothing) != "undefined" && doNothing();
const syncResolveProblem = syncResolved === true; // platform has sync resolve problem or not

type ResolveFunc<T> = (value: T | PromiseLike<T>) => void;
/**
 * @param timeout timeout time in ms, undefined if infinite
 */
function CreatePromiseData<T>(timeout?: number, opt: { fixSyncResolve?: boolean } = {}): IPromiseData<T> {
    const syncResolveFixer = (cb: ResolveFunc<T>) => syncResolveProblem && opt.fixSyncResolve ? (value: T | PromiseLike<T>) => setTimeout(() => cb(value)) : cb;
    let resolver: ResolveFunc<T> = <any>undefined, reject: (reason?: any) => void = <any>undefined;
    let timer: number;
    const startTimer: IPromiseData<T>["startTimer"] = (timeout: number) => {
        if (timer) { clearTimeout(timer as unknown as NodeJS.Timeout); }
        timer = setTimeout(() => { reject(new Error(`The operation has timed out. timeoutValue  : ${timeout}`)); }, timeout) as unknown as number;
    }

    if (timeout) {
        startTimer(timeout);
    }

    return {
        startTimer,
        promise: new Promise<T>((res, rej) => { resolver = syncResolveFixer(res); reject = rej; }),
        resolver,
        reject
    };
}

const selfSw = self as unknown as ServiceWorkerGlobalScope;
const waiters = new Map<string, IPromiseData<IResource>>();
let clients = new Map();

selfSw.addEventListener("install", (event) => {
    selfSw.skipWaiting();
});
selfSw.addEventListener("online", () => {
    // console.log("online", version);
});
selfSw.addEventListener("offline", () => {
    // console.log("offline", version);
});
selfSw.addEventListener("activate", (event) => {
    // console.log("activate", version);
});


selfSw.addEventListener("message", (event: ExtendableMessageEvent) => {
    const pack = event.data as serviceWorkerRequestResponse;
    const base64FileContent = getBase64FileContent(pack.resourceModel);
    if(checkBinaryFile(pack.resourceType)){
        let binResource = Buffer.from(base64FileContent, 'base64');
        let response: IResource = {
            resource: binResource,
            resourceId: pack.resourceId,
            resourceType: pack.resourceType
        }
    
        const waiter = waiters.get(pack.resourceId);
        waiter?.resolver(response);
    }
    else{
        let response: IResource = {
            resource: pack.resourceModel,
            resourceId: pack.resourceId,
            resourceType: pack.resourceType
        }
    
        const waiter = waiters.get(pack.resourceId);
        waiter?.resolver(response);
    }
});


const checkExtension = (fileName: string) => {
    const extension = fileName.substring(fileName.lastIndexOf(".") + 1, fileName.length);
    return extension as IImportFile["type"];
}

const getBase64FileContent = (fileContent) => {
    return fileContent.split(',')[1];
}

const checkClient = (clientId: string) => {
    return clients.has(clientId);
}

function checkBinaryFile(extensionOfFile: string) {
    return binaryFileTypes.includes(extensionOfFile);
  }

const run = async (event: FetchEvent) => {
    const url = new URL(event.request.url);
    const decodedPath = decodeURI(url.pathname);

    if(isStartWithLessSign(decodedPath)){
        const requestedInfo: IRequestInfo = parseURI(decodedPath);

        switch (requestedInfo.type) {
            case "img":

                const imgPromData = CreatePromiseData<IResource>(swTimeout);
                waiters.set(requestedInfo.guid, imgPromData);

                sendMessage(event.clientId, requestedInfo.guid);

                const imgResp = await imgPromData.promise;
                waiters.delete(requestedInfo.guid);

                let imgContentType = "image/" + imgResp.resourceType;                

                return new Response(imgResp.resource, { status: 200, headers:{ "Content-Type": imgContentType}});

            case "woff2":
            case "woff":
            case "ttf":
                const woffPromData = CreatePromiseData<IResource>(swTimeout);
                waiters.set(requestedInfo.guid, woffPromData);

                sendMessage(event.clientId, requestedInfo.guid);

                const woffResp = await woffPromData.promise;
                waiters.delete(requestedInfo.guid);

                let fontContentType = "application/font-" + woffResp.resourceType;
                return new Response(woffResp.resource, { status: 200, headers:{ "Content-Type": fontContentType}});
            default: break;
        }
    }
        const resp = await fetch(event.request);
        return resp;
}

const isStartWithQuick = (uri:string) => {
    if(uri.startsWith("/quick/")) return true;
    return false;
}

const isStartWithTest = (uri:string) => {
    if(uri.startsWith("/test-qeditor-plateau-test/")) return true;
    return false;
}

const isStartWithLessSign = (uri: string):boolean => {
    if(isStartWithQuick(uri)){
        uri = uri.substring(6, uri.length); 
    }

    if(isStartWithTest(uri)){
        uri = uri.substring(26, uri.length); 
    }

    if(uri[1] == "<" && uri[2] == "<"){
        return true;
    }

    return false;
}

const parseURI = (uri: string): IRequestInfo => {
    if(isStartWithQuick(uri)){
        uri = uri.substring(6, uri.length); 
    }

    if(isStartWithTest(uri)){
        uri = uri.substring(26, uri.length); 
    }
    const colonPos = uri.indexOf(":");
    const type = uri.slice(3,colonPos);
    const guid = uri.slice(colonPos+1, uri.length-2);

    return { type: type, guid: guid }
}


selfSw.addEventListener("fetch", (event) => {
    const resp = run(event);
    event.respondWith(resp);
});

const sendMessage = async (clientId:string, guid:string) => {
    const clients = await selfSw.clients.matchAll({  type: "window" });
    clients[0].postMessage(guid);
}


export {};